/**
 *     
 */
class CStorageController {
	/**
	 *      
	 * @param key - 
	 * @returns {*} 
	 * @private
	 */
	getFromStorage(key) {
		return localStorage.getItem(key);
	}

	/**
	 *      
	 * @param key - 
	 * @param data - 
	 * @private
	 */
	setToStorage(key, data) {
		return localStorage.setItem(key, data);
	}
}

export default CStorageController;
